.flip {
  width: 100%;
  max-width: 870px;
  font-family: var(--font-family-raleway);
  grid-template-rows: 265px;
  grid-template-columns: repeat(2, 1fr);
  gap: 1.5625rem;
  margin: 0 auto;
  display: grid;
}

.flip__item {
  cursor: pointer;
  position: relative;
}

.flip__item:before {
  content: "";
  z-index: 1;
  width: 54px;
  height: 54px;
  background-image: url("icons/refresh.fa3b60cd.png");
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
  margin: 0 auto;
  position: absolute;
  bottom: 19px;
  left: 0;
  right: 0;
}

.flip__item div {
  transition-property: transform, opacity;
  transition-duration: .5s;
  transition-timing-function: cubic-bezier(.175, .885, .32, 1.275);
}

.flip__item:hover .flip__front, .flip__item:focus .flip__front {
  transform: rotateY(180deg);
}

.flip__item:hover .flip__back, .flip__item:focus .flip__back {
  opacity: 1;
  transform: rotateY(0);
}

